
const toggle = document.querySelector('.hamburger-menu');
const overlay = document.querySelector('.overlay');

toggle.addEventListener('click', function () {
    overlay.classList.toggle('active');
});


const showreel_playBtn = document.getElementById("showreel_playBtn");
const showreel_video = document.getElementById("showreel_video");


function play_video(){
    if(showreel_video.paused){
        showreel_video.play();
    }
    else{
        showreel_video.pause();
    }
};

